/* buttons */
.btn { width: 150px; height: 43px; color: #FFF; background: #212121; padding: 0 15px; letter-spacing: 1px; }

.arrow-btn { height: 43px; color: #FFF; padding: 0 15px; padding-right: 43px; background: #212121; letter-spacing: 1px; }

.arrow-btn:after { width: 15px; height: 16px; right: 15px; margin-top: -8px; }

.plus-btn { border-radius: 32px; height: 43px; color: #FFF; background: #212121; padding: 0 15px; padding-left: 55px; letter-spacing: 1px; }

.plus-btn:after { width: 32px; height: 32px; left: 6px; margin-top: -16px; }

.v-btn, .f-btn, .o-btn, .t-btn { width: 2.75rem; height: 2.75rem; border-radius: 50%; background: #c4c4c4; border: 3px solid #212121; }

.v-btn::after, .f-btn::after, .o-btn::after, .t-btn::after { left: 50%; }

.v-btn:hover, .f-btn:hover, .o-btn:hover, .t-btn:hover { opacity: .75; }

.v-btn::after { width: 81.81818%; height: 72.72727%; margin: -31.81818% 0 0 -43.18182%; }

.f-btn::after { width: 56.81818%; height: 56.81818%; margin: -27.27273% 0 0 -29.54545%; }

.o-btn::after { width: 50%; height: 77.27273%; margin: -38.63636% 0 0 -25%; }

.t-btn::after { width: 72.72727%; height: 59.09091%; margin: -29.54545% 0 0 -36.36364%; }

.sc { display: flex; justify-content: center; list-style: none; }

.sc__item { flex: 0 1 auto; padding: 0 5px; }

.img-icon-btn { font-size: 18px; height: 3.33333em; border-radius: 0.44444em; padding: 0.11111em 1.77778em 0 4em; text-align: left; background: #ffbf00; color: #715502; }

.img-icon-btn__img { font-size: inherit; left: 2.05556em; width: 1.11111em !important; height: 1.66667em; margin-top: -0.88889em; }

.img-icon-btn__img img { max-height: 100%; }

.img-icon-btn:hover { opacity: .7; }

.link { color: #0000ff; border-bottom: 1px solid #0000ff; }

.link:hover { border-bottom-color: transparent; }

.green-link { color: #00ff00; border-bottom: 1px solid #00ff00; }

.green-link_dash { border-bottom-style: dashed; }

.green-link_rev { border-bottom-color: transparent; }

.green-link_rev:hover { border-bottom-color: #00ff00; }

.icon-link { border-bottom: 1px solid #0000ff; margin-left: 48px; }

.icon-link::before { width: 35px; height: 30px; margin-top: -15px; left: -48px; }

.icon-link:hover { border-bottom-color: transparent; }

.paginate { list-style: none; display: flex; align-items: center; justify-content: center; }

.paginate__item { flex: 0 1 auto; margin: 0 3px; }

.paginate__a, .paginate__curr, .paginate__dots { display: block; width: 36px; height: 36px; line-height: 36px; font-size: 21px; color: #000000; text-align: center; text-decoration: none; }

.paginate__a { opacity: .3; transition: .21s; }

.paginate__a:hover { opacity: .7; }

.paginate__prev, .paginate__next { display: block; width: 40px; height: 40px; background: #000000; margin: 0 7px; position: relative; transition: .21s; }

.paginate__prev::after, .paginate__next::after { width: 10px; height: 10px; border: 2px solid transparent; border-top-color: #FFF; border-left-color: #FFF; position: absolute; left: 50%; top: 50%; margin-top: -7px; }

.paginate__prev_disabled, .paginate__next_disabled { opacity: .2; }

.paginate__prev:hover, .paginate__next:hover { opacity: .7; }

.paginate__prev::after { margin-left: -3px; transform: rotate(-45deg); }

.paginate__next::after { margin-left: -10px; transform: rotate(135deg); }

/* icons */
.icon { min-height: 21px; padding-left: 30px; }

.icon:before { width: 25px; height: 21px; top: 0; }

.img-icon { position: relative; padding: 0.625em 0 0.625em 2.8125em; }

.img-icon__ico { display: block; position: absolute; left: 0; top: 0.0625em; width: 2em; height: 2em; }

.img-icon__ico span { display: block; }

.img-icon__ico img { max-height: 100%; }

.header { position: fixed; top: 0; left: 0; right: 0; z-index: 8; display: flex; height: 100px; align-items: center; transition: .5s; }

.header_fixed { background: #FFF; box-shadow: 0 1px 21px rgba(0, 0, 0, 0.21); }

.header_hide { top: -100px; }

