.slick-dots { z-index: 5; position: absolute; left: 0; bottom: 0; width: 100%; display: flex; align-items: center; justify-content: center; }

.slick-dots li { flex: 0 1 auto; list-style: none; padding: 0 0.375rem; }

.slick-dots button { display: block; font-size: 0; width: 1rem; height: 1rem; border-radius: 50%; border: none; background: rgba(255, 255, 255, 0.5); transition: .21s; }

.slick-dots button:hover { background: rgba(255, 255, 255, 0.75); }

.slick-dots .slick-active button { background: #FFFFFF; width: 1.25rem; height: 1.25rem; }

.slick-arrow { position: absolute; top: 50%; z-index: 3; width: 42px; height: 42px; margin-top: -21px; font-size: 0; transition: .21s; }

.slick-arrow:after { width: 16px; height: 16px; border: 4px solid transparent; border-top-color: #0000ff; border-left-color: #0000ff; position: absolute; left: 50%; top: 50%; margin-top: -12px; }

@media (min-width: 1201px) { .slick-arrow:hover { opacity: .7; } }

.slick-arrow.slick-disabled { opacity: .2; }

.slick-prev { left: -42px; }

.slick-prev:after { margin-left: -5px; transform: rotate(-45deg); }

.slick-next { right: -42px; }

.slick-next:after { margin-left: -18px; transform: rotate(135deg); }

.jspContainer { overflow: hidden; position: relative; }

.jspPane { position: absolute; }

.jspVerticalBar { position: absolute; top: 0; right: 0; width: 22px; height: 100%; }

.jspHorizontalBar { position: absolute; bottom: 0; left: 0; width: 100%; height: 16px; background: #ff0000; }

.jspCap { display: none; }

.jspHorizontalBar .jspCap { float: left; }

.jspTrack { background: linear-gradient(to bottom, #fff5a3, #fffbda); position: relative; border-radius: 20px; }

.jspDrag { background: #ff0000; position: relative; top: 0; left: 0; cursor: pointer; border-radius: 20px; }

.jspHorizontalBar .jspTrack, .jspHorizontalBar .jspDrag { float: left; height: 100%; }

.jspArrow { background: #50506d; text-indent: -20000px; display: block; cursor: pointer; padding: 0; margin: 0; }

.jspArrow.jspDisabled { cursor: default; background: #80808d; }

.jspVerticalBar .jspArrow { height: 16px; }

.jspHorizontalBar .jspArrow { width: 16px; float: left; height: 100%; }

.jspVerticalBar .jspArrow:focus { outline: none; }

.jspCorner { background: #eeeef4; float: left; height: 100%; }

.zoom { display: none; position: absolute; left: 0; top: 0; z-index: 99; width: 262px; height: 262px; border-radius: 50%; box-shadow: 0 0 21px rgba(0, 0, 0, 0.21); overflow: hidden; background: #ffffff; cursor: none; }

.zoom__inner { position: absolute; transform: scale(2); transform-origin: 0 0; }

.cursor { position: absolute; left: 0; top: 0; z-index: 99; width: 2.125rem; border-radius: 50%; background: rgba(255, 255, 255, 0.2); border: 1px solid #6c61a1; pointer-events: none; visibility: hidden; transition: width .35s; }

.cursor:before { display: block; content: ""; width: 100%; padding-bottom: 100%; }

.cursor[data-class="hover-a"] { width: 2.625rem; background: rgba(241, 89, 51, 0.7); border: none; }

.cursor[data-class="hover-btn"] { width: 2.625rem; border: 1px solid #ffffff; background: #aaaaaa; opacity: .5; }

.cursor_visible { visibility: visible; }

.cursor-wrap { position: fixed; z-index: 99; overflow: hidden; pointer-events: none; }

@media (min-width: 1101px) { .curs-main, .curs-btn, .curs-link { cursor: none; } }

.dragable_active { position: fixed; pointer-events: none; user-select: none; }

.dropable__mask { pointer-events: none; background: rgba(21, 128, 11, 0.5); }

/* main */
.main { flex: 1 0 auto; position: relative; overflow: hidden; }

.main, .main__full-page { padding-top: 100px; }

.main__full-page { overflow: hidden; }

/* first screen */
.first-screen { overflow: hidden; }

.first-screen > .row_col-middle { height: 100%; }

@media (min-width: 1201px) { .first-screen { height: 100vh; } }

/* section */
.section { padding-top: 30px; padding-bottom: 30px; }

/* list */
.list { list-style: none; }

.list li { padding-left: 1.875rem; position: relative; }

.list li:first-child { margin-top: 0; padding-top: 0; }

.list li:before { position: absolute; }

ul.list li:before { width: 1.25rem; height: 1.25rem; left: 0; top: 0; background: #ff0000; }

ol.list { counter-reset: listCounter; }

ol.list li:before { counter-increment: listCounter; content: counter(listCounter) "."; top: 0; left: 0; }

/* title */
.title { font-size: 30px; font-weight: 700; line-height: 1.2; margin-bottom: 1.16667em; }

/* article */
.article h2 { font-size: 22px; font-weight: 700; line-height: 1.2; margin: 45px 0 30px; }

.article h2:first-child { margin-top: 0; padding-top: 0; }

.article p { line-height: 1.35; margin-top: 25px; }

.article p:first-child { margin-top: 0; padding-top: 0; }

.article blockquote { line-height: 1.35; margin-top: 35px; }

.article blockquote:first-child { margin-top: 0; padding-top: 0; }

/* tile */
.tile-wrap > div { display: flex; }

.tile-wrap > div .tile { flex: 1 0 100%; }

